@import url(https://fonts.googleapis.com/css?family=Roboto);
/* RGB */
body {
  background-color: #ef767a;
  margin: 0, auto;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  color: #456990; }

.container {
  height: 100vh;
  padding: 1rem; }

#quote-box {
  width: 50%;
  margin: 0 auto;
  margin-top: 7rem;
  grid-area: app;
  padding: 1rem;
  grid-gap: 1rem;
  background-color: #2fe6de;
  display: grid;
  grid-template-columns: minmax(100px, auto);
  grid-template-rows: auto;
  grid-template-areas: "quote" "author" "buttons";
  box-shadow: 0 3px 2px #777; }

div p span {
  font-size: 5rem; }

#text {
  grid-area: quote;
  font-size: 1.7rem;
  text-align: center; }

#author {
  grid-area: author;
  text-align: center;
  align-self: center; }

.buttons {
  grid-area: buttons;
  padding: 1rem;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: auto auto; }
  .buttons a {
    justify-self: end; }
    .buttons a i {
      color: #456990;
      font-size: 1.8rem;
      border: none;
      padding: 0.4rem;
      background-color: #f2bc52; }
  .buttons #new-quote {
    justify-self: start; }
  .buttons button {
    color: #456990;
    background-color: #f2bc52;
    border: none;
    padding: 0.4rem;
    width: 8rem;
    cursor: pointer;
    font-size: 1rem; }

@media (max-width: 507px) {
  .buttons {
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    grid-gap: 0.1rem;
    padding: 0; } }

